import { defineStore } from "pinia";
import axios from "axios";

type User = {
    u_id: string,
    u_name: string,
    u_pwd: string
}

const useUserStore = defineStore('user_store', {
    state: () => ({
        users: [] as User[],
    }),
    getters: {
        getUsers: (s) => s.users,
    },
    actions: {

        /**
         * 
         */
        async request(): Promise<void> {
            try {
                this.users = await axios.get('http://localhost:8080/getUsers')
            } catch {
                this.users = [
                    {
                        u_id: '1 ',
                        u_name: ' ',
                        u_pwd: 'wow '
                    },
                    {
                        u_id: '2 ',
                        u_name: 'name 1 ',
                        u_pwd: '00 '
                    },
                    {
                        u_id: '3 ',
                        u_name: ' 1 ',
                        u_pwd: 'l '
                    }
                ]    
            }
        },

        /**
         * ID
         */
        searchByAnyField(keyword: string): User[] {
            return this.users.filter(e => e.u_id.includes(keyword) || e.u_name.includes(keyword) || e.u_pwd.includes(keyword))
        },

        /**
         * 
         * @see Promise<boolean>thenawait
         */
        async refresh(): Promise<boolean> {
            try {
                this.users = await axios.get('http://localhost:8080/getUsers')
            } catch {
                return false
            }
            return true
        },

        /**
         * 
         */
        remove(e: User) {
            console.log(e);
            
        }
    }
})

export { useUserStore }
export type { User }